import React, { useEffect, useState } from 'react'
import './style.css'

const Animationcomponent = (props: any) => {

    return (
        <div className={props.aniout ? `${props.className}+" "+ ${props.Animationtype}` : `${props.className}`} {...props}>
            {props.children}
        </div>
    )
}
export default Animationcomponent